import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {PatientsListComponent} from './myPatients/myPatients.component'
import { AddPatientsListComponent } from './addPatients/addPatients.component';
import { PatientsDetailsComponent } from './patientsLanding/patientsDetails.component';
import { AddwgssComponent } from './wgss/addwgss/addwgss.component';
import { EditwgssComponent } from './wgss/editwgss/editwgss.component';
import { ViewwgssComponent } from './wgss/viewwgss/viewwgss.component';
import { AddMedicalComponent } from './medicalHistory/addMedicalHistory/addMedical.component';
import { EditMedicalComponent } from './medicalHistory/editMedicalHistory/editMedical.component';
import { ViewMedicalComponent } from './medicalHistory/viewMedicalHistory/viewMedical.component';
import {AddFunctionalComponent} from './FunctionalAssessment/addFunctional/addFunctional.component';
import { EditFunctionalComponent } from './FunctionalAssessment/editFunctional/editFunctional.component';
import { ViewFunctionalComponent } from './FunctionalAssessment/viewFunctional/viewFunctional.component';
import { AddDevicesComponent } from './recommendDevices/addDevices/addDevices.component';




const routes: Routes = [
  {path:'', redirectTo:'patientslist',pathMatch:'full'},
  {path: 'patientslist',component: PatientsListComponent},
  {path: 'add-patients',component: AddPatientsListComponent},
  {path: 'patients-details',component:  PatientsDetailsComponent},
  {path: 'add-wgss',component:  AddwgssComponent},
  {path: 'edit-wgss',component:  EditwgssComponent},
  {path: 'view-wgss',component:  ViewwgssComponent},
  {path: 'add-medical',component:  AddMedicalComponent},
  {path: 'edit-medical',component:  EditMedicalComponent},
  {path: 'view-medical',component:  ViewMedicalComponent},
  {path: 'add-functional',component:  AddFunctionalComponent},
  {path: 'edit-functional',component:  EditFunctionalComponent},
  {path: 'view-functional',component:  ViewFunctionalComponent},
  {path: 'add-devices',component:  AddDevicesComponent},
  {path: '**', redirectTo:'/main/patients/patientslist'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class PatientsComponentsRoutingModule { 

}
