/**
 * Comman Methods
 * 
 */

  errorStatus(error:any){
    this.spinner.hide();
    this.snackBar.open((error.error.message[0]), '', {
      horizontalPosition: 'center',
      verticalPosition: 'bottom',
      duration: 3 * 1000,
    });
  }
