import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  name: 'genderPipeSingle',
})

export class GenderPipeSingle implements PipeTransform {
  transform(value: any): any {
    if (value  === 'male') {
      return 'M'
    } else if(value  === 'female') {
      return 'F';
    }
    else {
        return 'O'
    }
  }
}
