applyFilterCustom(item:any) {
  this.filterSerchFlag = 0;
  let index = this.filterData.findIndex((id:any)=> id?.type  == item.type);
  if(index == -1){
    this.filterData.push(item);
  } else {
    if(this.filterData[index].item_1 != item.item_1 || this.filterData[index].item_2 != item.item_2){
      this.filterData[index].item_1 = item.item_1;
      this.filterData[index].item_2 = item.item_2;
    }
  }

  this.genderDataFilter();
}

genderDataFilter(){
  let index = this.filterData.findIndex((id:any)=> id?.type  == 'gender');
  let filteredData;
  if(index != -1){
    filteredData = this.tabelData.filter((item:any) => item.gender == this.filterData[index].item_1);
    if(filteredData.length == 0) {
      this.filterSerchFlag = 1;
    } else {
      this.filterSerchFlag = 0;
    }
  } else {
    filteredData = [];
  }
  if(filteredData.length > 0) {
    this.ageDataFilter(filteredData);
  } else {
    console.log("test1");
    this.ageDataFilter(this.tabelData);
  }

}

ageDataFilter(data:any) {
  console.log("data1",data);
  let index = this.filterData.findIndex((id:any)=> id?.type  == 'age');
  let filteredData = [];
  if(index != -1){
    if(this.filterData[index].item_1 == ''){
      this.filterData[index].item_1 = undefined;
    }
    if(this.filterData[index].item_2 == '') {
      this.filterData[index].item_2 = undefined;
    }
    if( this.filterData[index].item_1 != undefined &&  this.filterData[index].item_2 != undefined) {
    filteredData = data.filter((item:any) => (item.age  > this.filterData[index].item_1 && item.age < this.filterData[index].item_2));
    if(filteredData.length == 0) {
      this.filterSerchFlag = 1;
     } else {
      this.filterSerchFlag = 0;
     }
    } else if( this.filterData[index].item_1 != undefined &&  this.filterData[index].item_2 == undefined) {
      filteredData = data.filter((item:any) => (item.age  > this.filterData[index].item_1));
      if(filteredData.length == 0) {
        this.filterSerchFlag = 1;
      } else {
        this.filterSerchFlag = 0;
      }
    }else if(this.filterData[index].item_1 == undefined && this.filterData[index].item_2 != undefined) {
      filteredData = data.filter((item:any) => (item.age < this.filterData[index].item_2));
      if(filteredData.length == 0) {
        this.filterSerchFlag = 1;
      } else {
        this.filterSerchFlag = 0;
      }
    } 
     else {
      filteredData = [];
    }
  }

  if(filteredData.length > 0) {
    this.statusDataFilter(filteredData);
  } else {
    this.statusDataFilter(data);
  }
 
}

